<template>
  <div>
    <nav-bar>
      <template v-slot:default>用户登录</template>
    </nav-bar>

    <div style="margin-top: 45px;">
      <div style="text-align: center;">
        <van-image
          width="10rem"
          height="10rem"
          fit="contain"
          src="https://cdn2.lmonkey.com/94f152aaa94d937ccf5de78f3fcac59f/3b6e32e7bc8145a283431f260c3c1d1a.png"
        />
      </div>

      <van-form @submit="onSubmit">
        <van-field
          v-model="email"
          name="用户名"
          label="用户名"
          placeholder="用户邮箱"
          :rules="[{ required: true, message: '请填写用户邮箱' }]"
        />
        <van-field
          v-model="password"
          type="password"
          name="密码"
          label="密码"
          placeholder="密码"
          :rules="[{ required: true, message: '请填写密码' }]"
        />
        <div style="margin: 16px;">
          <div class="link-login" @click="$router.push({path:'/register'})">
            立即注册
          </div>
          <van-button round block type="info" color="#42b983" native-type="submit">登录</van-button>
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>
  import NavBar from "components/common/navbar/NavBar";
  import {login} from "network/user";
  import { Notify, Toast } from 'vant';
  import {ref, reactive, toRefs} from 'vue';
  import {useRouter} from 'vue-router';
  import {useStore} from 'vuex'

  export default {
    name: "Login",
    components: {
      NavBar
    },
    setup(){
      const router = useRouter();
      const store = useStore();

      const userInfo = reactive({
        email: '',
        password: '',
      })

      const onSubmit = ()=>{
        login(userInfo).then(res=>{
          //将token保存在本地 window.localStorage setItem(key,value) getItem(key)
          window.localStorage.setItem('token',res.access_token);

          //在vuex中isLogin
          store.commit('setIsLogin', true)

          Toast.success('登录成功');

          userInfo.email = '';
          userInfo.password = '';

          setTimeout(()=>{
            router.go(-1);
          } ,500)
        })
      }

      return {
        ...toRefs(userInfo),
        onSubmit
      }
    }
  }
</script>

<style scoped>
.link-login {
  font-size: 14px;
  color: #3a7ef3;
  margin-bottom: 20px;
  /*display: inline-block;*/
  float: left;
}
</style>